body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

body {
  margin: 0 0 0 0;
  background-color: #f2f2f2;
  background-image: url("images/topography.svg");
}

h1 {
  font-family: 'Oswald';
}

h2 {
  font-family: 'Raleway';
}

.nav-bar {
  font-size: 40px;
  color: ghostwhite;
  width: 100%;
  height: 60px;
  background: rgba(93, 115, 126, 1);
}

.cover {
  display: -ms-flexbox;
  display: flex;
}

.cover-photo {
  height: 100vh;
}

.cover-title {
  margin: auto;
}

.cover-title h1 {
  font-size: 8vw;
}

.section-name {
  font-size: 2vw;
  border-style: solid;
  border-width: 1.5px;
  border-radius: 3px;
  cursor: pointer;
}

.pointer {
  cursor: pointer;
}

.section-name:hover {
  background: rgb(188,188,188, 0.5);
  border-color: rgb(0,0,0, 0.6);
}

.section-title {
  font-size: 5vw;
  font-family: 'Oswald';
}

.center {
  text-align: center;
  vertical-align: middle;
}

.footer {
  width: 100%;
  height: 100px;
}

.full-screen {
  height: 100vh;
  position: relative;
}



