.project-card {
    width: 100%;
    height: 130px;
    margin: 20px 0 20px 0;
    padding: 10px 10px 10px 10px;
    border-radius: 10px 10px 10px 10px;
    color: #f2f2f2;
}

.project-card:hover .overlay {
    opacity: 1;
}

.overlay {
    opacity: 0;
    -webkit-transition: .5s ease;
    -o-transition: .5s ease;
    transition: .5s ease;
}

.project-card-title {
    font-size: 25px;
    font-weight: bold;
    font-family: Raleway;
}

#estuary-jumbo {
    background: -webkit-linear-gradient(30deg, #3d606f 70%, #d5e4e6 30%);
    background: -o-linear-gradient(30deg, #3d606f 70%, #d5e4e6 30%);
    background: linear-gradient(60deg, #3d606f 70%, #d5e4e6 30%);
}

#probably-fine-jumbo {
    background: -webkit-linear-gradient(30deg, #cccccc 70%, #f2f2f2 30%);
    background: -o-linear-gradient(30deg, #cccccc 70%, #f2f2f2 30%);
    background: linear-gradient(60deg, #cccccc 70%, #f2f2f2 30%);
}



